'use client';

import dynamic from 'next/dynamic';
import { Component, HTMLAttributes } from 'react';

/* Import custom components */
import Filter4RPagePart from '@/components/filters/filter_4_r-pagePart/filter_4_r-pagePart';

/* Import styles */
import './annual_chart_1_r-pagePart.scss';

// Define event interfaces used in options
interface AxisXLabelFormatterEvent {
  value: number;
}

interface TooltipDataPoint {
  x: number;
  y: number;
}

interface TooltipEntry {
  dataPoint: TooltipDataPoint;
}

interface TooltipFormatterEvent {
  entries: TooltipEntry[];
}

interface AnnualChart1RPagePartProps {
  dataColor?: string;
  lineColor?: string;
  xData?: number[]; // Array for X-axis data
  yData?: number[]; // Array for Y-axis data
}

// Define the props interface for CanvasJSChart.
interface CanvasJSChartProps {
  options: CanvasJSChartOptions;
  containerProps?: HTMLAttributes<HTMLDivElement>;
}

// Define the interface for the chart options.
export interface CanvasJSChartOptions {
  animationEnabled: boolean;
  exportEnabled: boolean;
  backgroundColor: string;
  axisY: {
    minimum: number;
    maximum: number;
    interval: number;
    labelFontColor: string;
    titleFontColor: string;
    labelFontSize: number;
    titleFontSize: number;
    gridThickness: number;
    lineThickness: number;
    tickLength: number;
  };
  axisX: {
    interval: number;
    labelFontColor: string;
    titleFontColor: string;
    labelFontSize: number;
    titleFontSize: number;
    labelFormatter: (e: AxisXLabelFormatterEvent) => string;
  };
  toolTip: {
    shared: boolean;
    fontSize: number;
    backgroundColor: string;
    borderColor: string;
    contentFormatter: (e: TooltipFormatterEvent) => string;
  };
  legend: {
    fontSize: number;
  };
  data: Array<{
    type: string;
    showInLegend: boolean;
    markerSize: number;
    color: string;
    lineColor: string;
    fillOpacity: number;
    dataPoints: Array<{
      x: number;
      y: number;
    }>;
  }>;
}

// Dynamically import the CanvasJSChart component with SSR disabled.
// This attempts to support either a named export or a default export.
const CanvasJSChart = dynamic<CanvasJSChartProps>(
  () =>
    import('@canvasjs/react-charts').then((mod) => {
      return mod.CanvasJSChart || (mod.default && mod.default.CanvasJSChart);
    }),
  { ssr: false },
) as React.ComponentType<CanvasJSChartProps>;

class AnnualChart1RPagePart extends Component<AnnualChart1RPagePartProps> {
  static displayName = 'AnnualChart1RPagePart';
  render() {
    const { dataColor, lineColor, xData, yData } = this.props;

    // Create dataPoints dynamically from xData and yData
    const dataPoints =
      xData && yData && xData.length === yData.length
        ? xData.map((x, index) => ({
            x,
            y: yData[index] || 0, // Use 0 as default for missing Y data
          }))
        : [
            { x: 0, y: 150 },
            { x: 1, y: 160 },
            { x: 2, y: 140 },
            { x: 3, y: 130 },
            { x: 4, y: 120 },
            { x: 5, y: 110 },
            { x: 6, y: 115 },
            { x: 7, y: 125 },
            { x: 8, y: 135 },
            { x: 9, y: 145 },
            { x: 10, y: 155 },
            { x: 11, y: 165 },
          ]; // Default data if xData or yData is not provided

    const options: CanvasJSChartOptions = {
      animationEnabled: true,
      exportEnabled: false,
      backgroundColor: 'transparent',
      axisY: {
        minimum: 0,
        maximum: 200,
        interval: 50,
        labelFontColor: '#b3b3b3',
        titleFontColor: '#b3b3b3',
        labelFontSize: 10,
        titleFontSize: 10,
        gridThickness: 0,
        lineThickness: 0,
        tickLength: 0,
      },
      axisX: {
        interval: 1,
        labelFontColor: '#b3b3b3',
        titleFontColor: '#b3b3b3',
        labelFontSize: 10, // Smaller font
        titleFontSize: 10, // Smaller font
        labelFormatter: function (e) {
          const months = [
            'Jan',
            'Feb',
            'Mar',
            'Apr',
            'May',
            'Jun',
            'Jul',
            'Aug',
            'Sep',
            'Oct',
            'Nov',
            'Dec',
          ];
          return months[e.value];
        },
      },
      toolTip: {
        shared: true,
        fontSize: 10,
        backgroundColor: 'rgba(255, 255, 255, 0.8)',
        borderColor: '#ffffff',
        contentFormatter: function (e) {
          const months = [
            'Jan',
            'Feb',
            'Mar',
            'Apr',
            'May',
            'Jun',
            'Jul',
            'Aug',
            'Sep',
            'Oct',
            'Nov',
            'Dec',
          ];
          const dataPoint = e.entries[0];
          return `<div style='padding: 3px; border-radius: 3px; font-size: 10px;'>${months[dataPoint.dataPoint.x]}: ${dataPoint.dataPoint.y}</div>`;
        },
      },
      legend: {
        fontSize: 10,
      },
      data: [
        {
          type: 'splineArea',
          showInLegend: false,
          markerSize: 0,
          color: dataColor || '#000000', // Default to black if undefined
          lineColor: lineColor || '#000000', // Default to black if undefined
          fillOpacity: 0.4,
          dataPoints: dataPoints, // Use dynamically generated dataPoints
        },
      ],
    };

    return (
      <div
        className="annual_chart_1_r-pagePart"
        style={{
          width: '100%', // Full width
          height: '100%', // Fixed height
          background: 'var(--second-background-color)',
          padding: 'calc(1rem - 4px)',
          borderRadius: '5px',
          border: '1px solid var(--first-border-color)',
        }}
      >
        <div className="heading_cont">
          <div className="axis_y_title">Assets</div>

          {/* COMPONENT: Filter4RPagePart */}
          <div className="filter_cont">
            <div className="text">You have 21 assets of this month</div>
            <Filter4RPagePart
              filterInfoP={{
                name: 'Monthly',
                dropDownItems: [
                  {
                    text: 'Low',
                    id: 'low',
                  },
                  {
                    text: 'Medium',
                    id: 'medium',
                  },
                  {
                    text: 'High',
                    id: 'high',
                  },
                  {
                    text: 'Critical',
                    id: 'critical',
                  },
                  {
                    text: 'Informational',
                    id: 'informational',
                  },
                ],
              }}
            />
          </div>
        </div>
        <div style={{ width: '100%', height: '100%' }}>
          <CanvasJSChart
            options={options}
            containerProps={
              {
                width: '100%',
                height: '100%',
              } as unknown as HTMLAttributes<HTMLDivElement>
            }
          />
        </div>
      </div>
    );
  }
}

export default AnnualChart1RPagePart;
