/* Import medias */
import { ChevronDown } from 'lucide-react';

/* Import styles */
import styles from './filter_4_r-pagePart.module.scss';
import { useState } from 'react';

/* Define data type of props */
import { Filter4RPagePartType } from '@/interfaces';

interface Props {
  filterInfoP?: Filter4RPagePartType;
  Filter4RPagePartCallBackInfo?: object;
}

/**
 * This component supports passing additional styles via props and displays a default label if none is provided.
 *
 * @param  filterInfoP - Pass info here. If not provided, a default set of filter information will be used.
 * @param  Filter4RPagePartCallBackInfo - Pass the callback info (optional).
 *
 * @example
 * ```tsx
 * <Filter4RPagePart
 *   filterInfoP={{ name: "status", dropDownItems: [{ text: "Open", id: "open" }, { text: "Closed", id: "closed" }] }}
 * />
 * ```
 */
const Filter4RPagePart: React.FC<Props> = ({
  filterInfoP,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  Filter4RPagePartCallBackInfo,
}) => {
  const [showDropDown, setShowDropDown] = useState(false);

  const defaultFilterInfo: Filter4RPagePartType = {
    name: 'severity',
    dropDownItems: [
      {
        text: 'Low',
        id: 'low',
      },
      {
        text: 'Medium',
        id: 'medium',
      },
      {
        text: 'High',
        id: 'high',
      },
      {
        text: 'Critical',
        id: 'critical',
      },
      {
        text: 'Informational',
        id: 'informational',
      },
    ],
  };

  const filterInfo = filterInfoP || defaultFilterInfo; // Ensure the prop takes precedence over the default

  return (
    <div className={`${styles.filter_4_r_pagePart} filter_4_r_pagePart`}>
      {/* Main button */}
      <button
        className={`${styles.filter} filter`}
        onClick={() => {
          setShowDropDown(!showDropDown);
        }}
      >
        <div className={styles.name}>{filterInfo.name}</div>
        <div
          className={`${styles.icon} ${showDropDown ? styles.rotate : ''} icon_cont_type_1`}
        >
          <ChevronDown color="var(--second-text-color)" />
        </div>
      </button>
      {/* Dropdown */}
      {showDropDown && (
        <div className={styles.dropDown}>
          <ul>
            {filterInfo.dropDownItems.map((item, index) => (
              <li key={index}>
                <button id={item.id} onClick={() => {}}>
                  {item.text}
                </button>
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

Filter4RPagePart.displayName = 'Filter4RPagePart';

export default Filter4RPagePart;
