/* Import media */
import { Search } from 'lucide-react';

/* Import styles */
import styles from './search_bar_3_r-pagePart.module.scss';

/* Define data type of props */
interface Props {
  searchBar3PagePartCallBack?: (callbackInfo: { searchQuery: string }) => void;
}

/**

 * The callback receives an object with the current search query.
 * @example
 * ```tsx
 * <SearchBar3PagePart
 *   searchBar3PagePartCallBack={(callbackInfo) => console.log(callbackInfo.searchQuery)}
 * />
 * ```
 */

const SearchBar3PagePart: React.FC<Props> = (props) => {
  // Destructure prop
  const { searchBar3PagePartCallBack } = props;

  return (
    <form
      className={`${styles.frame15__frame12} SearchPagePart`}
      onSubmit={(e) => e.preventDefault()}
    >
      <button
        type="button"
        className={`${styles.frame15__frame12__frame13} submitBtn`}
      >
        <div
          className={`${styles.frame15__frame12__frame13__flowbiteSearchOutline} icon-cont`}
        >
          <Search color="var(--second-text-color)" />
        </div>
      </button>
      <input
        type="text"
        id="searchInput"
        name="searchInput"
        className={`${styles.frame15__frame12__search}`}
        placeholder="Search vulnerabilities"
        autoComplete="search"
        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
          let inputValue = event.target.value;

          // Convert input to string, trim, and escape
          inputValue = String(inputValue).trim();

          // console.log('ðŸš€ ~ searchQuery:', inputValue);

          if (searchBar3PagePartCallBack) {
            searchBar3PagePartCallBack({
              searchQuery: inputValue,
            });
          }
        }}
      />
    </form>
  );
};

export default SearchBar3PagePart;
