'use client';
import { useEffect, useRef } from 'react';
import { GridTable1RPagePartRowItemsType } from '@/interfaces';

/* Import medias */
import { GlobeLock } from 'lucide-react';

/* Import components */
import SearchBar4PagePart from '@/components/search_bars/search_bar_3_r-pagePart/search_bar_3_r-pagePart';
import Filter4RPagePart from '@/components/filters/filter_4_r-pagePart/filter_4_r-pagePart';

/* Import styles */
import styles from './gid_table_1_r-pagePart.module.scss';

/* Define data types */
/*interface GridTable1RPagePartRowItemsType {
  name: {
    statusType: 'pending';
    text: string;
  };
  type: {
    icon: ReactElement;
    text: string;
  };
  vulnerability: 'low' | 'medium' | 'high' | 'critical' | 'informational';
  assetGroup: string;
  lastUpdated: string;
}*/
interface Props {
  rowItemsP?: GridTable1RPagePartRowItemsType;
  GridTable1RPagePartCallBackInfo?: object;
}

/**
 * A component that displays a table of recent vulnerabilities.
 * Supports dynamic styling and allows additional data to be passed via props.
 *
 * @param rowItemsP - Data for the table rows.
 * @param GridTable1RPagePartCallBackInfo - Additional callback information.
 *
 * @example
 * ```tsx
 * <GridTable1RPagePart
 *   rowItemsP={{
 *     name: { statusType: 'pending', text: 'Backup directory - detected' },
 *     type: { icon: <GlobeLock color="var(--primary-color)" />, text: 'network' },
 *     vulnerability: 'medium',
 *     assetGroup: 'groupname',
 *     lastUpdated: '2d ago by John',
 *   }}
 *   GridTable1RPagePartCallBackInfo={{ someData: true }}
 * />
 * ```
 */

const GridTable1RPagePart: React.FC<Props> = ({
  rowItemsP,
  GridTable1RPagePartCallBackInfo,
}) => {
  const tableRef = useRef<HTMLDivElement>(null);

  // Define column headers
  const columnNames = [
    'Name',
    'Type',
    'Vulnerability',
    'Asset group',
    'Last updated',
  ];

  // Sample data for table rows
  const rowItems: GridTable1RPagePartRowItemsType[] = [
    {
      name: { statusType: 'pending', text: 'Backup directory - detected' },
      type: {
        icon: <GlobeLock color="var(--primary-color)" />,
        text: 'network',
      },
      vulnerability: 'low',
      assetGroup: 'groupname',
      lastUpdated: '2d ago by John',
    },
    {
      name: { statusType: 'pending', text: 'Backup directory - detected' },
      type: {
        icon: <GlobeLock color="var(--primary-color)" />,
        text: 'network',
      },
      vulnerability: 'medium',
      assetGroup: 'groupname',
      lastUpdated: '2d ago by Johnssssssssssssssssssssssss',
    },
    {
      name: { statusType: 'pending', text: 'Backup directory - detected' },
      type: {
        icon: <GlobeLock color="var(--primary-color)" />,
        text: 'network',
      },
      vulnerability: 'critical',
      assetGroup: 'groupname',
      lastUpdated: '2d ago by Johnssssssssssssssssssssssss',
    },
    {
      name: { statusType: 'pending', text: 'Backup directory - detected' },
      type: {
        icon: <GlobeLock color="var(--primary-color)" />,
        text: 'network',
      },
      vulnerability: 'informational',
      assetGroup: 'groupname',
      lastUpdated: '2d ago by Johnssssssssssssssssssssssss',
    },
    {
      name: { statusType: 'pending', text: 'Backup directory - detected' },
      type: {
        icon: <GlobeLock color="var(--primary-color)" />,
        text: 'network',
      },
      vulnerability: 'high',
      assetGroup: 'groupname',
      lastUpdated: '2d ago by Johnssssssssssssssssssssssss',
    },
  ];

  // Make the width som
  useEffect(() => {
    if (!tableRef.current) return;

    const columnsCell = tableRef.current.querySelectorAll(
      `.${styles.grid_cell}`,
    );
    const columnsWrapper = tableRef.current.querySelectorAll(
      `.${styles.grid_cell} .${styles.wrapper}`,
    );
    const columnWidths: number[] = Array(columnNames.length).fill(0);

    const updateColumnWidths = () => {
      // Reset widths before recalculating
      columnWidths.fill(0);

      // Measure wrapper widths instead of cell widths
      columnsWrapper.forEach((wrapper, index) => {
        const colIndex = index % columnNames.length;
        columnWidths[colIndex] = Math.max(
          columnWidths[colIndex],
          wrapper.getBoundingClientRect().width,
        );
      });

      // Apply the measured width to the grid cells
      columnsCell.forEach((cell, index) => {
        const colIndex = index % columnNames.length;
        (cell as HTMLElement).style.width = `${columnWidths[colIndex]}px`;
      });
    };

    const resizeObserver = new ResizeObserver(updateColumnWidths);

    columnsWrapper.forEach((wrapper) => resizeObserver.observe(wrapper));

    updateColumnWidths(); // Initial update

    return () => {
      resizeObserver.disconnect();
    };
  }, []);

  return (
    <div
      ref={tableRef}
      className={`${styles.grid_table_1_r_pagePart} border_type_1`}
    >
      <div className={styles.heading_cont}>
        <h2>Recent Vulnerabilities</h2>
        <div className={styles.filter_cont}>
          {/* COMPONENT: SearchBar4PagePart */}
          <SearchBar4PagePart />

          {/* COMPONENT: Filter4RPagePart */}
          <Filter4RPagePart />

          {/* COMPONENT: Filter4RPagePart */}
          <Filter4RPagePart
            filterInfoP={{
              name: 'status',
              dropDownItems: [
                { text: 'Open', id: 'open' },
                { text: 'Closed', id: 'closed' },
              ],
            }}
          />

          {/* COMPONENT: Filter4RPagePart */}
          <Filter4RPagePart
            filterInfoP={{
              name: 'status',
              dropDownItems: [
                { text: 'Open', id: 'open' },
                { text: 'Closed', id: 'closed' },
              ],
            }}
          />
        </div>
      </div>
      <div className={styles.grid_container}>
        {/*  Table header */}
        <div
          className={`${styles.grid_row} ${styles.grid_table_header_row}`}
          style={{
            gridTemplateColumns: `repeat(${columnNames.length}, min-content)`,
          }}
        >
          {columnNames.map((name, index) => (
            <div key={index} className={`${styles.grid_cell}`}>
              <div className={styles.wrapper}>{name}</div>
            </div>
          ))}
        </div>

        {/* Table body */}
        {rowItems.map((item, rowIndex) => (
          <button
            key={rowIndex}
            className={`${styles.grid_row} ${styles.grid_table_body_row}`}
            style={{
              gridTemplateColumns: `repeat(${columnNames.length}, min-content)`,
            }}
          >
            
            <div className={`${styles.grid_cell} ${styles.grid_cell_1}`}>
              <div className={styles.wrapper}>
                <div className={styles.status_1}>{item.name.statusType}</div>
                <div className={styles.text}>{item.name.text}</div>
              </div>
            </div>
            <div className={`${styles.grid_cell} ${styles.grid_cell_2}`}>
              <div className={styles.wrapper}>
                <div className={`${styles.icon} icon_cont_type_1`}>
                  {item.type.icon}
                </div>
              </div>
            </div>
            <div className={`${styles.grid_cell} ${styles.grid_cell_3}`}>
              <div className={styles.wrapper}>
                <div
                  className={`${styles.status_2} ${styles[`status_2_${item.vulnerability}`]}`}
                >
                  <div className={styles.round_shape}></div>
                  <div className={styles.text}>{item.vulnerability}</div>
                </div>
              </div>
            </div>
            <div className={`${styles.grid_cell} ${styles.grid_cell_4}`}>
              <div className={styles.wrapper}>
                <div className={styles.text}>{item.assetGroup}</div>
              </div>
            </div>
            <div className={`${styles.grid_cell} ${styles.grid_cell_5}`}>
              <div className={styles.wrapper}>
                <div className={styles.text}>{item.lastUpdated}</div>
              </div>
            </div>
          </button>
        ))}
      </div>
    </div>
  );
};

GridTable1RPagePart.displayName = 'GridTable1RPagePart';

export default GridTable1RPagePart;
